import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { EffectCoverflow, Navigation } from "swiper";
import { Link } from "react-router-dom";
import classNames from "classnames";

import "swiper/css";
import "swiper/css/effect-coverflow";
import "swiper/css/navigation";
import "./mainSlider.scss";
import image01 from "../../assets/images/main-slider/01.png";
import image02 from "../../assets/images/main-slider/02.png";
import image03 from "../../assets/images/main-slider/03.png";
import image01webp from "../../assets/images/main-slider/01.webp";
import image02webp from "../../assets/images/main-slider/02.webp";
import image03webp from "../../assets/images/main-slider/03.webp";

interface IMainSliderProps {
	className: string;
}

const slides = [
	{
		link: "/products/6",
		src: image01,
		webp: image01webp,
		alt: "відро арахісової пасти",
	},
	{
		link: "/products/2",
		src: image02,
		webp: image02webp,
		alt: "відро арахісової пасти",
	},
	{
		link: "/products/8",
		src: image03,
		webp: image03webp,
		alt: "відро арахісової пасти",
	},
];

const MainSlider: React.FC<IMainSliderProps> = React.memo(({ className }) => {
	const sliderClassnames = classNames(className, "main-slider");

	return (
		<Swiper
			className={sliderClassnames}
			modules={[EffectCoverflow, Navigation]}
			effect={"coverflow"}
			grabCursor={true}
			centeredSlides={true}
			slidesPerView={"auto"}
			coverflowEffect={{
				rotate: 0,
				stretch: 0,
				depth: 100,
				modifier: 10,
				slideShadows: false,
			}}
			navigation
			initialSlide={1}
			loop={true}
			loopedSlides={2}
			shortSwipes={false}
		>
			{slides.map(({ link, src, webp, alt }) => (
				<SwiperSlide className="main-slider__slide" key={link}>
					<Link className="main-slider__link" to={link}>
						<picture className="main-slider__image">
							<source srcSet={webp} type="image/webp" />
							<img src={src} alt={alt} />
						</picture>
					</Link>
				</SwiperSlide>
			))}
		</Swiper>
	);
});

export default MainSlider;
